var _ = require( 'underscore' );
var Utils = require( 'helpers/utils' );

var _instance;


function ScrollController() {

	this.canSmoothScroll = ( !global.props.ie && !global.props.ff && !global.props.mobile );

	this._destScrollY = 0;
	this._manualScrollTweener = null;
	this._autoScrollTweener = null;
	this._smoothScrollEnabled = false;
	this._manualScrollDisabled = false;

	this._$document = $( document );
	this._$window = $( window )
		.on( 'mousewheel', this.onMouseWheel.bind( this ) )
		.on( 'scroll', this.onScroll.bind( this ) );
};


ScrollController.prototype.resetSmoothScrollProps = function() {

	this._destScrollY = this._$window.scrollTop();
};


ScrollController.prototype.enableSmoothScroll = function() {

	this._smoothScrollEnabled = true;
};


ScrollController.prototype.disableSmoothScroll = function() {

	this._smoothScrollEnabled = false;
};


ScrollController.prototype.enableManualScroll = function() {

	this._manualScrollDisabled = false;

	if ( this._smoothScrollEnabled ) {
		this.resetSmoothScrollProps();
	}
};


ScrollController.prototype.disableManualScroll = function() {

	this._manualScrollDisabled = true;
};


ScrollController.prototype.scrollToAndLock = function( y, opt_duration, opt_onComplete ) {

	var duration = opt_duration || 2;

	if ( this._manualScrollTweener ) {
		this._manualScrollTweener.kill();
	}

	this._autoScrollTweener = TweenMax.to( window, duration, {
		scrollTo: {
			y: y,
			autoKill: false
		},
		ease: Quint.easeInOut,
		onComplete: function() {

			this.enableManualScroll();

			if ( opt_onComplete ) {
				opt_onComplete();
			}
		},
		onCompleteScope: this
	} );

	this.disableManualScroll();
};


ScrollController.prototype.onMouseWheel = function( e ) {

	if ( this._manualScrollDisabled ) {

		e.preventDefault();
		return false;
	}

	if ( this._smoothScrollEnabled ) {

		e.preventDefault();

	} else {

		return true;
	}

	var currentScrollY = this._$window.scrollTop();

	var delta = e.originalEvent.wheelDelta / 120 || -e.originalEvent.detail / 3;
	var nextScrollY = currentScrollY - parseInt( 50 * delta );

	var maxScrollY = this._$document.height() - this._$window.height();
	nextScrollY = Utils.clamp( nextScrollY, 0, maxScrollY );

	if ( ( delta < 0 && nextScrollY <= this._destScrollY ) ||
		( delta > 0 && nextScrollY >= this._destScrollY ) ) {

		return;

	} else {

		this._destScrollY = nextScrollY;
	}

	var duration = Utils.lerp( .25, 1, Math.min( 1, Math.abs( this._destScrollY - currentScrollY ) / 1000 ) );

	this._manualScrollTweener = TweenMax.to( window, duration, {
		scrollTo: {
			y: this._destScrollY,
			autoKill: true,
			onAutoKill: this.resetSmoothScrollProps,
			onAutoKillScope: this
		},
		overwrite: 'preexisting'
	} );
};


ScrollController.prototype.onScroll = function( e ) {

	this.scrollTop = this._$window.scrollTop();

	if ( !this._smoothScrollEnabled ) {

		return true;
	}

	if ( !this._manualScrollTweener || !this._manualScrollTweener.isActive() ) {

		this.resetSmoothScrollProps();
	}
};



module.exports = Utils.createSingletonNow( _instance, ScrollController );