var Utils = require( './utils' );
var Signal = require( 'signals' );

var _instance;


var PubSub = function() {

	this.anyMediaPlayed = new Signal();

	this.anyContentLiked = new Signal();

	this.soundChanged = new Signal();
	this.soundChanged.memorize = true;

	this.prevSoundClicked = new Signal();
	this.nextSoundClicked = new Signal();
	this.soundToggled = new Signal();

	this.shineRenderedOnce = new Signal();
	this.shineRenderedOnce.memorize = true;

	this.submissionToBePosted = new Signal();
};


module.exports = Utils.createSingletonNow( _instance, PubSub );