var io = require( 'socket.io-client' );
var _ = require( 'underscore' );
var Utils = require( 'helpers/utils' );

var _instance;


function SocketController() {

	var io = require( 'socket.io-client' );
	var EVENTS = global.config.EVENTS;

	var namespaces = {
		'chat': null,
		'submission': null
	};

	this._sockets = _.mapObject( namespaces, function( val, key ) {

		var url = global.config.WS_URI + '/' + key;
		var socket = io.connect( url );

		return socket;
	} );
};


SocketController.prototype.getSocketByNamespace = function( namespace ) {

	return this._sockets[ namespace ];
};


module.exports = Utils.createSingleton( _instance, SocketController );